<template>
    <div>
        <b-navbar toggleable="lg" type="dark" variant="info">
            <b-navbar-brand href="#">Secret files</b-navbar-brand>
            <b-navbar-nav class="ml-auto">
                <b-navbar-brand href="#">
                    <i
                      :class="isLogin ? 'fa-user-check' : 'fa-user-slash'"
                      class="fas"></i>
                </b-navbar-brand>
                <b-nav-item-dropdown :disabled="!isLogin" right>
                    <template slot="button-content">
                        <em>{{userEmail}}</em>
                    </template>
                    <b-dropdown-item @click="logout">Logout</b-dropdown-item>
                </b-nav-item-dropdown>
            </b-navbar-nav>
        </b-navbar>
    </div>
</template>

<script>
    export default {
        name: "NavBar",
        computed: {
            isLogin() {
                return this.$store.getters.isLogin
            },
            userEmail() {
                return this.$store.getters.userEmail
            }
        },
        methods: {
            logout() {
                if (this.$cookies.isKey("keyName")) {
                    this.$cookies.remove("keyName");
                    this.$store.commit("setEmail", "empty");
                    this.$store.commit("changeIcon");
                    this.$router.push({ path: 'login' });
                }
            }
        },
        created() {
            if (this.$cookies.isKey("keyName")) {
                this.$store.commit("setEmail", this.$cookies.get("keyName"));
                this.$store.commit("changeIcon");
            }
        }
    }
</script>

