<template>
    <div>
        <b-table striped hover :fields="fields" :items="files">
            <template slot="id" slot-scope="data">
                <a :href="`#/list/${data.value.toLowerCase()}`">{{ data.value }}</a>
            </template>
        </b-table>
    </div>
</template>

<script>

    import fileList from '../serverRequest';

    export default {
        name: "ListPage",
        data: () => {
            return {
                fields: [
                    'id',
                    'name',
                ],
                files: [],
            }
        },
        created() {
            if (!this.$cookies.isKey("keyName")) {
                this.$router.push({ path: 'login' });
            }

            fileList("http://localhost:8080/list").then((res) => {
                this.files = res;
             });
        }
    }
</script>

