export default {
    state: {
        isLogin: false,
        userEmail: "empty"
    },
    mutations: {
        changeIcon(state) {
            state.isLogin = !state.isLogin
        },
        setEmail(state, newValue) {
            state.userEmail = newValue
        }

    },
    actions: {},
    getters:{
        isLogin (state) {
            return state.isLogin
        },
        userEmail (state) {
            return state.userEmail
        }
    }
}
