<template>
    <div>
        <b-table striped hover :items="files">
        </b-table>
        <router-link to="/list">
            <b-button variant="info">back</b-button>
        </router-link>
    </div>
</template>

<script>
    import fileList from '../serverRequest';

    export default {
        name: "IdPage",
        data: () => {
            return {
                files: []
            }
        },
        created() {
            const id = this.$route.params.id;
            fileList("http://localhost:8080/list/" + id).then((res) => {
                this.files = res;
            });
        }
    }
</script>

